  -moz-osx-font-smoothing: "Roboto", sans-serif;
  font-style: normal;
}

h1,h2,h3,h4,h5,h6,p {
  margin: 0;
  color: var(--white);
}
#error{
  position: absolute;
  background-color: #000000;
  color: red;
  z-index: 999;
}
