:root {
  --primary: #1db954; /* Neon Green */
  --heading-color: #8ced8c; /* Light Pastel Green */
  --bg-shade: #1e1e1e; /* Dark Blackish Background */
  --github: #1db954; /* Spotify Green */
  --darkblue: #0d0d0d; /* Deep Black */
  --black: #000000; /* Pure Black */
  --white: #e6e6e6; /* Light Grey (not pure white for softer contrast) */
}

.btn {
  font-family: "Roboto";
  display: inline-block;
  padding: 14px 32px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  border-radius: 4px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  transition: background-color 0.5s;
  -webkit-transition: background-color 0.5s;
  -moz-transition: background-color 0.5s;
  -ms-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
}
.btn-outline-primary:hover {
  color: var(--primary);
  background-color: var(--white);
  border: 1px solid var(--primary);
}
.btn-outline-primary {
  color: var(--white);
  background-color: var(--primary);
}
.btn-primary:hover {
  background-color: var(--white);
  color: var(--primary);
  border: 1px solid var(--primary);
}
.btn-primary {
  background-color: var(--primary);
  color: var(--white);
}
.btn-github {
  color: var(--white);
  background-color: var(--github);
  border: var(--github);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.95);
  gap: 16px;
  font-weight: 600;
}
.btn-github:hover {
  color: var(--github);
  background-color: var(--white);
}

.hero--section--btn{
  color: var(--white);
  background-color: var(--primary);
  border: 1px solid var(--primary);
}

.hero--section--btn:hover {
  color: var(--primary);
  background-color: var(--white);
}

.logo--section{
  display: flex;
  align-items: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
}

.link--home{
  display: flex;
  align-items: center;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
}

.logo{
  width: 50px;
  height: 50px;
}

a{
  text-decoration: none;
}
h1 {
  font-size: 56px;
  font-weight: 700;
  line-height: 67px;
}
h2 {
  font-size: 48px;
  font-weight: 700;
  line-height: 58px;
}
h2 {
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
}
.text-lg {
  color: var(--white);
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 27px;
}
.text-md {
  color: var(--white);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}
.text-sm {
  color: var(--white);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}
.section--title {
  color: var(--white);
  font-size: 21px;
  text-align: center;
  font-style: normal;
  font-weight: 600;
  line-height: 32px;
  display: flex;
  align-items: flex-start;
}
.sub--title {
  color: var(--white);
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 85.333px;
  background: var(--black);
  box-shadow: 0px 5.333px 80px 0 rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
}
.navbar--items > ul {
  list-style: none;
  display: flex;
  align-items: flex-start;
  gap: 42.667px;
  text-decoration: none;
  background-color: var(--black);
}
.navbar--items ul > li > a {
  text-decoration: none;
}
.navbar--content {
  color: var(--white);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  cursor: pointer;
}
.navBar--active-content {
  color: var(--primary);
}
.hero--section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 133.333px 85.333px 133.333px;
  align-items: center;
  justify-content: space-between;
  gap: 32px;
  background-color: var(--bg-shade);
}
.hero--section--content-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
}
.hero--section--content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 21.333px;
}
/* .hero--section--content--box > button {
  margin-top: 21.333px;
} */
 
.hero--section--title {
  color: var(--white);
  font-size: 74.667px;
  font-weight: 700;
  line-height: 90px;
  align-self: stretch;
}
.hero--section--title--color {
  color: var(--primary);
}
.hero--section-description {
  color: var(--white);
  font-size: 24px;
  font-weight: 400;
  line-height: 36px;
  text-align: justify;
  padding-right: 50px;
}
.hero--section--img {
  display: flex;
}
.hero--section--img > img {
  width: 100%;
  height: 100%;
}

.hero--btn{
  margin-top: 21.333px;
}

/* skill card styling  starts*/
.skills-card{
  border-radius:0.65rem;
  border:1.5px solid var(--primary);
  padding: 2.5rem;
  text-align: center;
  background:var(--bg-shade);
  color: var(--white);
  font-weight: 600;
  backdrop-filter: blur(1rem);
  position: relative;
  cursor: pointer;
  transition: all 0.3s ease;
}

.skills-card:hover,.skills-card.active{
  background: linear-gradient(100deg, var(--heading-color),var(--github));
  color: var(--black);
  border:1.5px solid var(--white);
}

.skills-card span{
  font-size: 1.3rem;
  font-weight: 500;
}
.skill-icon{
  width: 4rem;
  height: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--bg-shade);
  border-radius: 0.65rem;
  border: 1.5px solid var(--primary);
  position: absolute;
  top: -1rem;
  left: -1rem;
}
.skill-icon img{
  width: 2.4rem;
  height: auto;
  object-fit: contain;
}
@media (max-width:1025px){
  .skills-card{
    padding: 2rem;
  }
  .skills-card span{
    font-size: 1rem;
  }
  .skill-icon{
    width: 3rem;
    height: 3rem;
  }
  .skill-icon img{
    width: 1.8rem;
  }
}
@media (max-width:768px){
  .skills-card span{
    font-size: 0.9rem;
  }
}

/* skill card styling ends */
/* skill styling starts*/
.skills-container{
  padding: 149.33px 85.33px;
  gap: 106.667px;
  position: relative;
  background: var(--bg-shade);
}

.skills-container::after,.skills-container::before{
  content:" ";
  width: 28rem;
  height: 28rem;
  border-radius: 28.125rem;
  background: var(--bg-shade);
  position: absolute;
  z-index: -1;
  filter: blur(200px);
}

.skills--section--heading {
  color: var(--primary);
  font-size: 54px;
  font-weight: 700;
  margin-bottom: 3.5rem;
  line-height: 77px;
}

.skills-content{
  display: flex;
  align-items: flex-start;
  gap: 3rem;
}

.skills{
  flex: 1;
  display: grid;
  grid-gap: 3rem;
  grid-template-columns: repeat(2,1fr);
}

.skills-info{
  flex: 1;
}

@media (max-width:1025px){
  .skills{
    padding-left: 1rem;
  }
  .skills,.skills-content{
    grid-gap: 2rem;
  }
}

@media (max-width:768px){
  .skills-content{
    flex-direction: column-reverse;
    gap: 3rem;
  }
  .skills,.skills-info{
    width: 100%;
  }
  .skills-container h5{
    font-size: 1.3rem;
    margin-bottom: 2rem;
  }
  .skills-container::after,.skills-container::before{
    width: 18rem;
    height: 18rem;
  }
}

@media (max-width:438px){
  .skills{
    display: flex;
    flex-direction: column;
  }
}

/* skill styling ends */
/* skill info card styling starts */
.skills-info-card{
  min-height:23rem;
  border-radius: 0.65rem;
  border:1.5px solid var(--primary);
  background:var(--bg-shade);
  backdrop-filter: blur(1rem);
}

.skills-info-card h6{
  font-size: 1.2rem;
  font-weight:600;
  padding: 0.8rem 2rem;
  background:linear-gradient(180deg,var(--white)0%,var(--white)100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  border-bottom: 1.5px solid var(--primary);
}
.skills-info-content{
  padding: 2rem;
}
.skill-info{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.skill-info p{
  font-size: 1rem;
  font-weight: 500;
}
.skill-info .percentage{
  color: var(--github);
}
.skill-progress-bg{
  width: 100%;
  height: 0.5rem;
  background: var(--white);
  border-radius: 0.5rem;
  margin: 1rem 0;
  overflow: hidden;
}
.skill-progress{
  width: 0%;
  height: 0.5rem;
  background: linear-gradient(90deg, var(--heading-color)-50%,var(--primary)50%);
  border-radius: 0.5rem;
  transition: all 0.5s ease-in-out;
}
@media (max-width:1025px){
  .skill-info p{
    font-size: 0.9rem;
  }
  .skills-info-content{
    padding: 1.5rem;
  }
}
/* skill info card styling ends */


